.gpt3__features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.gpt3__features-heading {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    
    text-align: left;
    margin-right: 5rem;
}

.gpt3__features-heading h1 {
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
    text-align: left;

}

.gpt3__features-heading p {
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
    text-align: left;
    color: #FF8A71;
    margin-top: 2rem;
}

.gpt3__features-container {
    display: flex;
    flex: 1.5;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (min-width: 2000px) {
    .gpt3__features {
        max-width: 2000px;
    }
}


@media screen and (max-width: 990px) {
    .gpt3__features {
        flex-direction: column;
    }

    .gpt3__features-heading {
        margin: auto;
    }

    .gpt3__features-heading h1 {
        text-align: center;
    }

    .gpt3__features-heading p {
        margin-bottom: 2rem;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__features-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
}